<template>
  <section :class="cname">
    <swiper :options="options" :not-next-tick="options.notNextTick" class="swiper">
      <swiper-slide v-for="item in items" :key="item.href">
        <router-link :to="{name: item.href}" class="link">
          <img :src="item.src" alt="" class="img">
        </router-link>
      </swiper-slide>
      <div v-if="options.pagination" class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </section>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    cname: {
      type: String,
      default: ''
    },
    options: {
      type: Object,
      default () {
        return {
          loop: true,
          autoplay: true,
          pagination: {
            el: '.swiper-pagination'
          },
          notNextTick: false
        }
      }
    },
    items: {
      type: Array,
      default () {
        return []
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~swiper/dist/css/swiper.css';
.swiper{
  .link{
    display: block;
  }
}
</style>




